<script>
export default {
  props: {
    name: { type: String, required: true },
    data: {
      type: Object,
      default: () => {
        return { hoursAngle: 0, minutesAngle: 0, secondsAngle: 0 };
      },
    },
  },
  computed: {
    diferenceSeconds() {
      return this.calculeAngle(this.data.secondsAngle);
    },
    diferenceMinutes() {
      return this.calculeAngle(this.data.minutesAngle);
    },
    diferenceHours() {
      return this.calculeAngle(this.data.hoursAngle);
    },
  },
  methods: {
    calculeAngle(value) {
      if (value >= 0 && value <= 90) return -value / 9;
      if (value > 90 && value <= 180) return -(value + 90) / 18;
      if (value > 180 && value <= 270) return -(-value + 270) / 6;
      else return 0;
    },
  },
};
</script>